@mixin tether-theme-basic($themePrefix: "tether", $themeName: "basic", $backgroundColor: #fff, $color: inherit) {
    .#{ $themePrefix }-element.#{ $themePrefix }-theme-#{ $themeName } {
        max-width: 100%;
        max-height: 100%;

        .#{ $themePrefix }-content {
            border-radius: 5px;
            box-shadow: 0 2px 8px rgba(0, 0, 0, .2);
            font-family: inherit;
            background: $backgroundColor;
            color: $color;
            padding: 1em;
            font-size: 1.1em;
            line-height: 1.5em; } } }



