
// Fonts
@import url('https://fonts.googleapis.com/css?family=Josefin+Sans:400,400i,700,700i|Noto+Sans:400,400i,700,700i');
// Bootstrap
@import "~bootstrap/scss/bootstrap";
@import "~bootstrap/scss/bootstrap-grid";
@import "~bootstrap/scss/bootstrap-reboot";

// select2 for bootstrap
@import '~select2/src/scss/core';
@import '~select2-bootstrap4-theme/src/select2-bootstrap4';

// font awesome 5
@import "~fontawesome5-webfont/scss/fontawesome";
@import "~fontawesome5-webfont/scss/fa-solid";
@import "~fontawesome5-webfont/scss/fa-regular";
@import "~fontawesome5-webfont/scss/fa-brands";

// pc-bootstrap4-datetimepicker
@import "~pc-bootstrap4-datetimepicker/src/sass/bootstrap-datetimepicker-build";

// sweetalert2
@import '~sweetalert2/src/sweetalert2';

// tether
@import '~tether/src/css/tether';
@import '~tether/src/css/tether-theme-arrows';
@import '~tether/src/css/tether-theme-arrows-dark';
@import '~tether/src/css/tether-theme-basic';


// pretty checkbox	
@import '~pretty-checkbox/src/pretty-checkbox.scss';

body {
	//background-color: #0B8165;
	background: url(images/background.jpg);
	background-attachment: fixed;
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center center;
	width: 100%;
	font-family: 'Noto Sans', sans-serif;
}

h1, h2, h3, h4, h5, h6 {
	font-family: 'Josefin Sans', sans-serif;
	text-shadow: 4px 4px 4px #aaa;
}
